import { HeroesActionTypes, Hero } from './types'

export function fetchRequest() {
  return { type: HeroesActionTypes.FETCH_REQUEST } 
}

export function fetchSuccess(data: Hero[]) {
  return {
    type: HeroesActionTypes.FETCH_SUCCESS,
    payload: data
  }
}

export function fetchError (message: string) {
  return {
    type: HeroesActionTypes.FETCH_ERROR,
    payload: message
  }
}

