import * as React from 'react'
import { connect } from 'react-redux'

import { ApplicationState, ConnectedReduxProps } from '../store'
import { Hero } from '../store/heroes/types'
import { fetchRequest } from '../store/heroes/actions'

interface PropsFromState {
  loading: boolean
  data: Hero[]
  errors?: string
}

interface PropsFromDispatch {
  fetchRequest: typeof fetchRequest
}

type AllProps = PropsFromState & PropsFromDispatch & ConnectedReduxProps


class Main extends React.Component<AllProps> {

  state = {
    token: ''
  }

  componentDidMount() {
    var ws = new WebSocket('ws://localhost:40510');
    // event emmited when connected
    ws.onopen = function () {
      console.log('websocket is connected ...')
      // sending a send event to websocket server
      ws.send('connected')
    }
    // event emmited when receiving message 
    ws.onmessage = function (req) {
      console.log(req.data);
    }
  }

  private eventClickButton = () => {
    const token = this.state.token;
    localStorage.setItem('token', token);
  }

  public render() {
    return(
      <div>
        <input
          name="text-input-token"
          placeholder="Введите токен"
          onChange={e => this.setState({ token: e.target.value })}
        />
        <button type="submit" onClick={this.eventClickButton}>Сохранить</button>
      </div>
    )
  }

}


const mapStateToProps = ({ heroes }: ApplicationState) => ({
  loading: heroes.loading,
  errors: heroes.errors,
  data: heroes.data
})

const mapDispatchToProps = {
  fetchRequest
}


export default connect(
  mapStateToProps, 
  mapDispatchToProps)(Main)


   